import { IndividualPlayer } from "./CustomTypes";
import Airtable from "airtable";

const databaseID = 'appve6uoBia2hPeKb';
const tableID = 'tblYr1AOES1Iosjp5';

export async function retrievePlayers(passkey: string): Promise<string[]> {

    let response: string[] = []; // Add default value of an empty array
    Airtable.configure({
        endpointUrl: 'https://api.airtable.com',
        apiKey: passkey
    });

    var base = Airtable.base(databaseID);

    await base(tableID).select({maxRecords: 1000}).all()
        .then(records => {
            response = records.map((record: any) => record.fields);
         });

    return response;
}

export function exportPlayers(players: Map<Number, IndividualPlayer>, getplayerAttribute: Function, passkey: string) {

    const myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${passkey}`);
    myHeaders.append("Content-Type", "application/json");

    let recordlist: { fields: { Name: string; ID: number; Wins: number; Losses: number; Rating: any; "Rating Deviation": any; Volatility: any; }; }[] = [];
    players.forEach((player) => {
        recordlist.push({
            "fields": {
                "Name": player.Name,
                "ID": player.id,
                "Wins": player.wins,
                "Losses": player.losses,
                "Rating": getplayerAttribute(player.MMRObject, "rating"),
                "Rating Deviation": getplayerAttribute(player.MMRObject, "ratingDeviation"),
                "Volatility": getplayerAttribute(player.MMRObject, "volatility")
            }
        });
    });

    // Send the records in batches of 10
    const batchsize = 10;
    const batches = Math.ceil(recordlist.length / batchsize);
    for (let i = 0; i < batches; i++) {
        const batch = recordlist.slice(i * batchsize, (i + 1) * batchsize);
        const raw = JSON.stringify({
            "performUpsert": {
                "fieldsToMergeOn": [
                    "ID"
                ]
            },
            "records": batch
        });

        const requestOptions = {
            method: "PATCH",
            headers: myHeaders,
            body: raw,
        };

        fetch(`https://api.airtable.com/v0/${databaseID}/${tableID}`, requestOptions)
            .then((response) => response.text())
            .then((result) => console.log(result))
            .catch((error) => console.error(error));
    }
    

    // const header = ["Name", "ID", "Wins", "Losses", "Rating", "Rating Deviation", "Volatility"]

    // // Convert the map to an array of records
    // const records = Array.from(players.values()).map(player => Object.values(player));

    // // // Iterate over the records and unpack the MMRObjects
    // records.forEach((record, index) => {
    //     const player = Array.from(players.values())[index];
    //     const MMRObject = [getplayerAttribute(player.MMRObject, "rating"), getplayerAttribute(player.MMRObject, "ratingDeviation"), getplayerAttribute(player.MMRObject, "volatility")];
    //     // Join the record with the MMRObject attributes
    //     records[index] = [...record, ...MMRObject];
    //     // remove item at index 4 from records
    //     records[index] = records[index].filter((_, i) => i !== 4);
    // });

    // console.log(records);

}