import { DataGrid } from '@mui/x-data-grid';
import { useEffect, useState } from 'react';

import AccountCircle from '@mui/icons-material/AccountCircle';
import { Box, Button, Divider, Drawer, FormControl, InputAdornment, InputLabel, List, ListItem, MenuItem, Select, TextField } from '@mui/material';
import Player, { Match } from 'glicko-two';
import { IndividualPlayer } from './CustomTypes';
import { exportPlayers, retrievePlayers } from './SheetsIntegration';

const tempMap = new Map<Number, IndividualPlayer>();
const defaultTau = 0.5;
const john: IndividualPlayer =
{
    Name: "John",
    id: 3,
    wins: 0,
    losses: 0,
    MMRObject: new Player({
        defaultRating: 1500,
        rating: 999,
        ratingDeviation: 350,
        tau: defaultTau,
        volatility: 0.06,
    }),
}
tempMap.set(john.id, john);

function getplayerAttribute(MMRObject: Player | undefined, att: "rating" | "volatility" | "ratingDeviation") {
    if (MMRObject === undefined) {
        return "N/A";
    }
    return MMRObject.toJSON()[att];
}

function getPlayersasList(players: Map<Number, IndividualPlayer>) {
    return Array.from(players.values());
}

function getPlayerNames(players: Map<Number, IndividualPlayer>) {
    let namelist = Array.from(players.values()).map((player) => player.Name);
    // Order alphabetically
    namelist.sort();
    return namelist;
}

export default function PlayerTable() {
    const [players, setPlayers] = useState<Map<Number, IndividualPlayer>>(tempMap);
    const [loading, setLoading] = useState(true);
    const [showMatchupPanel, setShowMatchupPanel] = useState(false);
    const [showNewPlayerPanel, setShowNewPlayerPanel] = useState(false);
    const [newPlayerName, setNewPlayerName] = useState<string>("");

    const [matchUpP1, setMatchUpP1] = useState<string>("");
    const [matchUpP2, setMatchUpP2] = useState<string>("");
    const [matchUpP3, setMatchUpP3] = useState<string>("");
    const [matchUpP4, setMatchUpP4] = useState<string>("");
    const [matchUpWinner, setMatchUpWinner] = useState<string>("");
    const [passkey, setPasskey] = useState<string>("");

    useEffect(() => {
        setLoading(true);
    }, []);

    let addPlayer = (name: string) => {
        if (name === "") {
            console.log("Name is undefined");
        }
        else {
            const newPlayer = {
                Name: name,
                id: tempMap.size + 1,
                wins: 0,
                losses: 0,
                MMRObject: new Player({
                    defaultRating: 1500,
                    rating: 1500,
                    ratingDeviation: 350,
                    tau: 0.5,
                    volatility: 0.06,
                }),
            }
            setPlayers(players.set(newPlayer.id, newPlayer))
        }
    }

    let playerNametoID = (name: string) => {
        return Array.from(players.values()).find((player) => player.Name === name)?.id;
    }

    let executeMatchup = () => {
        const P1 = players.get(playerNametoID(matchUpP1) ?? 0);
        const P2 = players.get(playerNametoID(matchUpP2) ?? 0);
        const P3 = players.get(playerNametoID(matchUpP3) ?? 0);
        const P4 = players.get(playerNametoID(matchUpP4) ?? 0);

        if (P1 === undefined || P2 === undefined || P3 === undefined || P4 === undefined) {
            console.log("One or more players not found");
            return;
        }
        else {
            const aTeam = [P1.MMRObject, players.get(playerNametoID(matchUpP2) ?? 0)?.MMRObject];
            const bTeam = [players.get(playerNametoID(matchUpP3) ?? 0)?.MMRObject, players.get(playerNametoID(matchUpP4) ?? 0)?.MMRObject];
            const matchup = new Match(aTeam.filter(Boolean) as Player[], bTeam.filter(Boolean) as Player[]);

            if (matchUpWinner === "A") {
                matchup.reportTeamAWon();
                // Update the player wins and losses
                P1.wins++;
                P2.wins++;
                P3.losses++;
                P4.losses++;
            }
            else {
                matchup.reportTeamBWon();
                // Update the player wins and losses
                P1.losses++;
                P2.losses++;
                P3.wins++;
                P4.wins++;
            }
            matchup.updatePlayerRatings();

            // Reset the matchup panel
            setMatchUpP1("");
            setMatchUpP2("");
            setMatchUpP3("");
            setMatchUpP4("");
            setMatchUpWinner("");
        }
    }

    let loadPlayers = async () => {
        let loadMap = new Map<Number, IndividualPlayer>();
        let rawPlayers: any[];

        Promise.resolve(
            await retrievePlayers(passkey)
                .then((res) => { rawPlayers = res; })
                .catch((error) => console.error(error))
        ).then(() => {
            rawPlayers.forEach((player) => {
                const newPlayer = {
                    Name: player.Name,
                    id: Number(player.ID),
                    wins: Number(player.Wins),
                    losses: Number(player.Losses),
                    MMRObject: new Player({
                        defaultRating: 1500,
                        rating: player.Rating,
                        ratingDeviation: player["Rating Deviation"],
                        tau: defaultTau,
                        volatility: player.Volatility,
                    }),
                }
                loadMap.set(newPlayer.id, newPlayer);
            });
            setPlayers(loadMap);
        }).then(() => { console.log("Players Loaded", players) })
            .then(() => { setLoading(false) });
    }

    return (
        // Display a datagrid of players
        <div style={{ height: "90vh", width: '100%' }}>
            <DataGrid
                rows={getPlayersasList(players)}
                columns={[
                    { field: 'Name', headerName: 'Name', width: 150 },
                    { field: 'MMRObject', headerName: 'Rating', width: 150, valueGetter: (params) => Math.round(Number(getplayerAttribute(params, "rating"))) },
                    { field: 'id', headerName: 'Deviation', width: 150, valueGetter: params => Math.round(Number(getplayerAttribute(players.get(params)?.MMRObject ?? undefined, "ratingDeviation"))) },
                    // { field: 'id', headerName: 'ID', width: 150 },
                ]}
                loading={loading}
                initialState={{ sorting: { sortModel: [{ field: 'MMRObject', sort: 'desc' }] } }}
            />
            <Box height={"10vh"} display="flex" alignItems="center" justifyContent="center">
                <Button onClick={() => { setShowMatchupPanel(!showMatchupPanel); setShowNewPlayerPanel(false) }}>New Matchup</Button>
                <Button onClick={() => { setShowNewPlayerPanel(!showMatchupPanel); setShowMatchupPanel(false) }}>New Player</Button>
                {!loading && <Button onClick={() => { exportPlayers(players, getplayerAttribute, passkey) }}>Save Database</Button>}
                {loading && <Button onClick={() => { loadPlayers() }}>Load Database</Button>}
                <TextField value={passkey} onChange={(e) => { setPasskey(e.target.value) }} type='password' label="Password" />
            </Box>

            <Drawer open={showMatchupPanel} onClose={() => { setShowMatchupPanel(false) }} anchor='right' variant='persistent'>
                <Box sx={{ width: "20vw" }} role="presentation">
                    <List>
                        <Divider>Team 1</Divider>
                        <ListItem>
                            <FormControl fullWidth>
                                <InputLabel id="NewMatchup">Player 1</InputLabel>
                                <Select id="T1P1" label="Player 1" value={matchUpP1} onChange={(e) => { setMatchUpP1(e.target.value) }}>
                                    {getPlayerNames(players).map((name, index) => <MenuItem key={index} value={name}>{name}</MenuItem>)}
                                </Select>
                            </FormControl>
                        </ListItem>

                        <ListItem>
                            <FormControl fullWidth>
                                <InputLabel id="NewMatchup">Player 2</InputLabel>
                                <Select id="T1P2" label="Player 2" value={matchUpP2} onChange={(e) => { setMatchUpP2(e.target.value) }}>
                                    {getPlayerNames(players).map((name, index) => <MenuItem key={index} value={name}>{name}</MenuItem>)}
                                </Select>
                            </FormControl>
                        </ListItem>

                        <Divider>Team 2</Divider>

                        <ListItem>
                            <FormControl fullWidth>
                                <InputLabel id="NewMatchup">Player 1</InputLabel>
                                <Select id="T2P1" label="Player 1" value={matchUpP3} onChange={(e) => { setMatchUpP3(e.target.value) }}>
                                    {getPlayerNames(players).map((name, index) => <MenuItem key={index} value={name}>{name}</MenuItem>)}
                                </Select>
                            </FormControl>
                        </ListItem>

                        <ListItem>
                            <FormControl fullWidth>
                                <InputLabel id="NewMatchup">Player 2</InputLabel>
                                <Select id="T2P2" label="Player 2" value={matchUpP4} onChange={(e) => { setMatchUpP4(e.target.value) }}>
                                    {getPlayerNames(players).map((name, index) => <MenuItem key={index} value={name}>{name}</MenuItem>)}
                                </Select>
                            </FormControl>
                        </ListItem>

                        <Divider>Outcome</Divider>

                        <ListItem>
                            <FormControl fullWidth>
                                <InputLabel id="Winner">Winner</InputLabel>
                                <Select id="Winner" label="Winner" value={matchUpWinner} onChange={(e) => { setMatchUpWinner(e.target.value) }}>
                                    <MenuItem key="T1" value="A">Team 1</MenuItem>
                                    <MenuItem key="T2" value="B">Team 2</MenuItem>
                                </Select>
                            </FormControl>
                        </ListItem>

                        <ListItem>
                            <Button onClick={() => { console.log("Submit Matchup"); setShowMatchupPanel(false); executeMatchup() }}>Submit Matchup</Button>
                        </ListItem>
                    </List>
                </Box>
            </Drawer>

            <Drawer open={showNewPlayerPanel} onClose={() => { setShowNewPlayerPanel(false) }} anchor='right' variant='persistent'>
                <Box sx={{ width: "20vw" }} role="presentation">
                    <List>
                        <Divider>Add New Player</Divider>

                        <ListItem>
                            <FormControl fullWidth>
                                <TextField
                                    required
                                    label="Player Name"
                                    value={newPlayerName}
                                    onChange={(e) => { setNewPlayerName(e.target.value) }}
                                    InputProps={{
                                        startAdornment: (
                                            <InputAdornment position="start">
                                                <AccountCircle />
                                            </InputAdornment>
                                        ),
                                    }}
                                />
                            </FormControl>
                        </ListItem>

                        <ListItem>
                            <Button onClick={() => { addPlayer(newPlayerName); setShowNewPlayerPanel(false); setNewPlayerName("") }}>Save Player</Button>
                        </ListItem>
                    </List>
                </Box>
            </Drawer>
        </div>
    );
}